import React from 'react';
import { Link } from 'react-router-dom';
import styles from './reset-password-page.module.css';
import { Input, Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { request } from '../../utils/api-config';
import useForm from '../../hooks/use-form';

function ResetPasswordPage() {
  const { values, handleChange } = useForm({
    code: '',
    newPassword: '',
  });

  const newPasswordData = {
    password: values.newPassword,
    token: values.code,
  };

  const handleResetPassword = () => {
    request('/password-reset/reset', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(newPasswordData),
    })
      .then((data) => {
        if (data.success) {
          console.log('  :', data);
        } else {
          console.error('  :', data.message);
        }
      })
      .catch((error) => {
        console.error('   :', error);
      });
  };

  return (
    <div className={styles.page}>
      <form className={styles.resetPasswordForm} onSubmit={handleResetPassword}> 
        <h2 className={styles.headerText}> </h2>
        <Input
          id='newPassword'
          type='password'
          placeholder='  '
          onChange={handleChange}
          value={values.newPassword}
          name="newPassword"
          extraClass={styles.input}
        />
        <Input
          id='code'
          placeholder='   '
          onChange={handleChange}
          value={values.code}
          name="code"
          extraClass={styles.input}
        />
        <Button
          htmlType="button"
          type="primary"
          size="small"
          extraClass={styles.resetPasswordButton}
          onClick={handleResetPassword}
        >
          C
        </Button>
      </form>
      <p className={styles.additionalText}> ? <Link to="/login"> </Link></p>
    </div>
  );
}

export default ResetPasswordPage;
